<template>
  <div class="layout-div">
    <b-navbar variant="faded" type="light">
      <b-navbar-nav>
        <b-nav-text>&nbsp;&nbsp;&nbsp;&nbsp;l&nbsp;&nbsp;&nbsp;&nbsp;      </b-nav-text>
      </b-navbar-nav>

      <b-navbar-nav class="ml-auto">
        <b-nav-item-dropdown text=" 10" right>
          <b-dropdown-item href="#">10</b-dropdown-item>
          <b-dropdown-item href="#">20</b-dropdown-item>
          <b-dropdown-item href="#">30</b-dropdown-item>
          <b-dropdown-item href="#">40</b-dropdown-item>
        </b-nav-item-dropdown>

        <b-link class="logout"></b-link>
      </b-navbar-nav>
    </b-navbar>

    <b-table :items="items" :fields="fields" id="board-table"></b-table>

    <b-row>
      <b-col cols="4">
        <b-row align-h="start" class="search-row">
          <b-col cols="3">
            <b-dropdown id="dropdown-1" text="+" width="100%">
              <b-dropdown-item>+</b-dropdown-item>
              <b-dropdown-item></b-dropdown-item>
              <b-dropdown-item></b-dropdown-item>
              <b-dropdown-item></b-dropdown-item>
              <b-dropdown-item> </b-dropdown-item>
              <b-dropdown-item> </b-dropdown-item>
            </b-dropdown>
          </b-col>

          <b-col cols="6">
            <b-form-input width="100%"></b-form-input>
          </b-col>

          <b-col cols="3" id ="search-button">
            <b-button variant="outline-primary" width="100%"></b-button>
          </b-col>
        </b-row>
      </b-col>

      <b-col cols="7">
        <b-pagination
          v-model="currentPage"
          :total-rows="rows"
          :per-page="perPage"
          aria-controls="board-table"
        ></b-pagination>
      </b-col>

      <b-col cols="1">
        <b-button variant="outline-primary"></b-button>
      </b-col>
    </b-row>

  </div>
</template>

<script>
    export default {
      name: "Layout",
      data() {
          return {
            fields: ['writingNum', 'title', 'writer', 'date', 'view', 'up'],
            items: [
              { writingNum: 14317, title: '   ', writer: ' ', date: '19-04-10', view: 21706, up: 2 },
              { writingNum: 14316, title: '    .gif', writer: '', date: '19-04-10', view: 24708, up: 0 },
              { writingNum: 14315, title: '  ', writer: '', date: '19-04-10', view: 14312, up: 0 },
              { writingNum: 14314, title: '  ', writer: '', date: '19-04-10', view: 19460, up: 0 },
            ],
            currentPage: 1,
            perPage: 10,
          };
      },
      computed: {
        rows() {
          return this.items.length
        }
      }
    }
</script>

<style scoped>

  .layout-div {
    margin: 10px 30px 30px ;
  }

  .logout {
    padding-top: 7px;
  }

  #board-table {
    line-height: 1;
  }

  #search-button {
    padding-left: 0;
  }


</style>



